import React, { Component } from "react";
import Stepper from "react-stepper-horizontal";

import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import validationSupplier from "../../Validations/validationSupplier";
import { AxiosMasterGet } from "../../Axios";
import { renderField, renderFieldSelect } from "../../renderField";

class FormSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      step: 0,
      listKota: [],
      listProvinsi: [],
      listKecamatan: [],
      status: "AKTIF",
      step1: "row",
      step2: "row d-none",
      step3: "row d-none",
    };
  }

  nextStep = () => {
    const { step } = this.state;
    if (step === 0) {
      this.setState({ step: step + 1, step2: "row", step1: "row d-none" });
    } else if (step === 1) {
      this.setState({ step: step + 1, step2: "row d-none", step3: "row" });
    }
  };

  prevStep = () => {
    const { step } = this.state;
    if (step === 1) {
      this.setState({ step: step - 1, step2: "row d-none", step1: "row" });
    } else if (step === 2) {
      this.setState({ step: step - 1, step3: "row d-none", step2: "row" });
    }
  };

  componentDidMount() {
    AxiosMasterGet("kota/get/all")
      .then((res) =>
        this.setState({
          listKota: res.data,
        })
      )
      .catch((err) => console.log(err));
    AxiosMasterGet("provinsi/get/all")
      .then((res) =>
        this.setState({
          listProvinsi: res.data,
        })
      )
      .catch((err) => console.log(err));
    AxiosMasterGet("kecamatan/get/all")
      .then((res) =>
        this.setState({
          listKecamatan: res.data,
        })
      )
      .catch((err) => console.log(err));
  }

  onProvinsiSelected(e) {
    console.log(e);
    AxiosMasterGet("kota/get/all")
      .then((res) =>
        this.setState({
          listKota: res.data,
        })
      )
      .then(() =>
        this.setState({
          listKota: this.state.listKota.filter(
            (list) => list.kode_provinsi === e
          ),
        })
      )
      .then(() => this.props.change("kodeKota", ""))
      .catch((err) => console.log(err));
    AxiosMasterGet("kecamatan/get/all")
      .then((res) =>
        this.setState({
          listKecamatan: res.data,
        })
      )
      .catch((err) => console.log(err));
  }

  onKotaSelected(e) {
    console.log(e);
    AxiosMasterGet("kecamatan/get/all")
      .then((res) =>
        this.setState({
          listKecamatan: res.data,
        })
      )
      .then(() =>
        this.setState({
          listKecamatan: this.state.listKecamatan.filter(
            (list) => list.kode_kota === e
          ),
        })
      )
      .then(() => this.props.change("kodeKecamatan", ""))
      .catch((err) => console.log(err));
    console.log(this.state.listKecamatan);
  }

  render() {
    return (
      <div className="col-lg-12 col-md-12">
        <form onSubmit={this.props.handleSubmit}>
          <div className="col-md-12">
            <Stepper
              steps={[
                { title: "Data Diri" },
                { title: "Contact Person" },
                // { title: "Data Bank" },
              ]}
              activeStep={this.state.step}
            />
          </div>
          <div className={this.state.step1} id="step-0">
            <div className="col-md-4 col-lg-4">
              <Field
                name="kodeSupplier"
                type="text"
                component={renderField}
                label="Kode Supplier"
                limit="5"
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-md-2 col-lg-2">
              <Field
                name="kodeExt"
                type="text"
                component={renderField}
                label="Kode Ext"
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-md-6 col-lg-6">
              <Field
                name="namaSupplier"
                type="text"
                component={renderField}
                label="Nama Supplier"
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                component={renderFieldSelect}
                label="Provinsi"
                name="kodeProvinsi"
                value={this.props.status}
                normalize={(value) => value && value.toUpperCase()}
                className="form-control"
                options={this.state.listProvinsi.map((obj) => {
                  var payload = {
                    value: obj.kode_provinsi,
                    name: obj.nama_provinsi,
                  };
                  return payload;
                })}
                onChange={(e) => this.onProvinsiSelected(e)}
              ></Field>
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                component={renderFieldSelect}
                label="Kota"
                name="kodeKota"
                value={this.props.status}
                normalize={(value) => value && value.toUpperCase()}
                className="form-control"
                options={this.state.listKota.map((obj) => {
                  var payload = {
                    value: obj.kode_kota,
                    name: obj.nama_kota,
                  };
                  return payload;
                })}
                onChange={(e) => this.onKotaSelected(e)}
              ></Field>
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                component={renderFieldSelect}
                label="Kecamatan"
                name="kodeKecamatan"
                value={this.props.status}
                normalize={(value) => value && value.toUpperCase()}
                className="form-control"
                options={this.state.listKecamatan.map((obj) => {
                  var payload = {
                    value: obj.kode_kecamatan,
                    name: obj.nama_kecamatan,
                  };
                  return payload;
                })}
              ></Field>
            </div>

            <div className="col-md-10 col-lg-10">
              <Field
                name="Alamat"
                type="text"
                component={renderField}
                label="Alamat"
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-md-2 col-lg-2">
              <Field
                name="kodePos"
                type="number"
                component={renderField}
                label="Kode Pos"
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="text-right">
                <button
                  type="button"
                  className="btn btn-primary"
                  onClick={() => this.nextStep()}
                >
                  Next
                </button>
              </div>
            </div>
          </div>
          <div className={this.state.step2} id="step-1">
            <div className="col-md-4 col-lg-4">
              <Field
                name="contactPerson"
                type="text"
                component={renderField}
                label="Contact Person"
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                name="Email"
                type="email"
                component={renderField}
                label="Email"
                normalize={(value) => value && value.toUpperCase()}
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                name="telepon"
                type="number"
                component={renderField}
                label="Telepon"
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                name="fax"
                type="number"
                component={renderField}
                label="Fax"
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                name="handPhone"
                type="number"
                component={renderField}
                label="Handphone"
              />
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="row">
                <div className="text-left col-md-6">
                  <button
                    name="simpan"
                    id="simpan"
                    type="button"
                    className="btn btn-primary"
                    onClick={() => this.prevStep()}
                  >
                    Back
                  </button>
                </div>
                <div className="text-right col-md-6">
                  <button
                    name="simpan"
                    id="simpan"
                    type="submit"
                    className="btn btn-primary"
                    onClick={() =>
                      this.setState({
                        step1: "row",
                        step2: "row d-none",
                        step3: "row d-none",
                        step: 0,
                      })
                    }
                  >
                    Simpan
                  </button>
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}
FormSupplier = reduxForm({
  form: "SupplierTambah",
  validate: validationSupplier,
  enableReinitialize: true,
})(FormSupplier);
export default connect()(FormSupplier);
