import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import FormSupplier from "./Component/FormSupplier";
import { connect } from "react-redux";
import { editsupplier, getsupplierList } from "../actions/supplierAction";
import notrespond from "../Images/500.svg";
import Swal from "sweetalert2";
import FormSupplierEdit from "./Component/FormSupplierEdit";
import { reset } from "redux-form";
import Skeleton from "react-loading-skeleton";
import paginationFactory from "react-bootstrap-table2-paginator";
import { AxiosMasterDelete, AxiosMasterPost, AxiosMasterPut } from "../Axios";
import { berhasil, gagal, LogData, reactive } from "../Alert";
import { hideModal, showModal } from "../actions/ModalAction";
import ModalMasterData from "./Component/ModalMasterData";
import HeaderCard from "../HeaderCard";
import TourGuide from "../TourGuide";
import { finishSend, progressSend } from "../actions/LogicAction";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      AxiosMasterDelete("supplier/delete/" + kode)
        .then(() => dispatch(hideModal()))
        .then((res) =>
          berhasil("Data Berhasil Dihapus!").then(() =>
            dispatch(getsupplierList())
          )
        )
        .catch((err) => gagal("Data Gagal Dihapus!"));
    }
  });
}

const expandRow = {
  renderer: (row) => (
    <div>
      <table>
        <tr
          style={{ backgroundColor: "transparent", borderColor: "transparent" }}
        >
          <td>Kode Ext</td>
          <td>:</td>
          <td>{row.kode_ext}</td>
        </tr>
        <tr
          style={{ backgroundColor: "transparent", borderColor: "transparent" }}
        >
          <td>Email</td>
          <td>:</td>
          <td>{row.email}</td>
        </tr>
        <tr
          style={{ backgroundColor: "transparent", borderColor: "transparent" }}
        >
          <td>Alamat</td>
          <td>:</td>
          <td>{row.alamat}</td>
        </tr>
        <tr
          style={{ backgroundColor: "transparent", borderColor: "transparent" }}
        >
          <td>Kode Pos</td>
          <td>:</td>
          <td>{row.kode_pos}</td>
        </tr>
        <tr
          style={{ backgroundColor: "transparent", borderColor: "transparent" }}
        >
          <td>Contact Person</td>
          <td>:</td>
          <td>{row.contact_person}</td>
        </tr>
        <tr
          style={{ backgroundColor: "transparent", borderColor: "transparent" }}
        >
          <td>Telephone</td>
          <td>:</td>
          <td>{row.no_telp}</td>
        </tr>
        <tr
          style={{ backgroundColor: "transparent", borderColor: "transparent" }}
        >
          <td>Fax</td>
          <td>:</td>
          <td>{row.fax}</td>
        </tr>
        <tr
          style={{ backgroundColor: "transparent", borderColor: "transparent" }}
        >
          <td>Handphone</td>
          <td>:</td>
          <td>{row.no_hp}</td>
        </tr>
      </table>
    </div>
  ),
};
const maptoState = (state) => {
  return {
    supplierList: state.provinsi.supplier,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    onSend: state.provinsi.onSend,
  };
};

class Supplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "kode_supplier",
          text: "Kode Supplier",
        },
        {
          dataField: "nama_supplier",
          text: "Nama Supplier",
        },
        {
          dataField: "kode_provinsi",
          text: "Kode Provinsi",
        },
        {
          dataField: "kode_kota",
          text: "Kode Kota",
        },
        {
          dataField: "kode_kecamatan",
          text: "Kode Area",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kode_supplier)}
                  data-tut="reactour_3"
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  className="btn btn-warning"
                  data-tut="reactour_4"
                  onClick={() =>
                    this.props.dispatch(
                      editsupplier(
                        row.alamat,
                        row.email,
                        row.contact_person,
                        row.fax,
                        row.no_hp,
                        row.kode_ext,
                        row.kode_kota,
                        row.kode_pos,
                        row.kode_supplier,
                        row.nama_supplier,
                        row.no_telp,
                        row.kode_provinsi,
                        row.kode_kecamatan,
                        this.editModal()
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-2"></i>
                  EDIT
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getsupplierList());
  }

  handleSubmit(hasil) {
    let data = {
      kode_supplier: hasil.kodeSupplier,
      nama_supplier: hasil.namaSupplier,
      kode_ext: hasil.kodeExt,
      kode_provinsi: hasil.kodeProvinsi,
      kode_kota: hasil.kodeKota,
      kode_kecamatan: hasil.kodeKecamatan,
      alamat: hasil.Alamat,
      kode_pos: hasil.kodePos,
      contact_person: hasil.contactPerson,
      no_telp: hasil.telepon,
      no_hp: hasil.handPhone,
      fax: hasil.fax,
      email: hasil.Email,
    };
    let dataEdit = {
      nama_supplier: hasil.namaSupplier,
      kode_ext: hasil.kodeExt,
      kode_provinsi: hasil.kodeProvinsi,
      kode_kota: hasil.kodeKota,
      kode_kecamatan: hasil.kodeKecamatan,
      alamat: hasil.Alamat,
      kode_pos: hasil.kodePos,
      contact_person: hasil.contactPerson,
      no_telp: hasil.telepon,
      no_hp: hasil.handPhone,
      fax: hasil.fax,
      email: hasil.Email,
    };
    this.props.dispatch(progressSend());
    AxiosMasterPost("supplier/add", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Ditambah!")
          .then(() => LogData("Data Berhasil Ditambah Di Supplier", data))
          .then(() => this.props.dispatch(getsupplierList()))
          .then(() => this.props.dispatch(reset("SupplierTambah")))
      )
      .catch((err) => this.handleReactive(err, hasil.kodeSupplier, dataEdit));
  }
  handleReactive(err, kode, data) {
    this.props.dispatch(hideModal());
    let error = err.response.data;
    let check = error.includes("deleted");
    check
      ? reactive(
          err,
          kode,
          "supplier/reactive/",
          data,
          "supplier/edit/"
        ).then(() => this.props.dispatch(getsupplierList()))
      : gagal("Data Gagal Ditambahkan");
  }

  handleEdit(hasil) {
    let data = {
      nama_supplier: hasil.namaSupplier,
      kode_ext: hasil.kodeExt,
      kode_provinsi: hasil.kodeProvinsi,
      kode_kota: hasil.kodeKota,
      kode_kecamatan: hasil.kodeKecamatan,
      alamat: hasil.Alamat,
      kode_pos: hasil.kodePos,
      contact_person: hasil.contactPerson,
      no_telp: hasil.telepon,
      no_hp: hasil.handPhone,
      fax: hasil.fax,
      email: hasil.Email,
    };
    this.props.dispatch(progressSend());
    AxiosMasterPut("supplier/edit/" + hasil.kodeSupplier, data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Diubah!")
          .then(() => LogData("Data Berhasil Diubah Di Supplier", data))
          .then(() => this.props.dispatch(getsupplierList()))
      )
      .catch((err) =>
        gagal("Data Gagal Diubah!")
          .then(() => LogData("Data Gagal Diubah Di Supplier", data, err))
          .then(() => this.props.dispatch(hideModal()))
      );
  }

  tambahModal() {
    this.props.dispatch(showModal());
    this.setState({
      isEdit: false,
    });
  }
  editModal() {
    this.props.dispatch(showModal());
    this.setState({
      isEdit: true,
    });
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true"></span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <TourGuide
          tourConfig={[
            `Untuk Menambahkan Data Supplier, Tekan Tombol ini Dan Isi kolom yang disediakan`,
            `Ini adalah Data Supplier yang sudah tersimpan`,
            `Ini adalah tombol untuk menghapus data pada baris yang dipilih`,
            `Ini adalah tombol untuk mengubah data pada baris yang dipilih`,
          ]}
        />
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Data Supplier" />
              <div className="card-body">
                <ModalMasterData
                  content={
                    this.state.isEdit ? (
                      <FormSupplierEdit
                        onSend={this.props.onSend}
                        onSubmit={(data) => this.handleEdit(data)}
                      />
                    ) : (
                      <FormSupplier
                        onSubmit={(data) => this.handleSubmit(data)}
                        onSend={this.props.onSend}
                      />
                    )
                  }
                  title={
                    this.state.isEdit
                      ? "Edit Data Supplier"
                      : "Tambah Data Supplier"
                  }
                />
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button
                      name="simpan"
                      id="simpan"
                      type="button"
                      className="btn btn-info"
                      onClick={() => this.tambahModal()}
                      data-tut="reactour_1"
                    >
                      Tambah Data Supplier
                    </button>
                  </div>
                </div>
                <div data-tut="reactour_2">
                  {this.props.supplierList ? (
                    <ToolkitProvider
                      keyField="kode_supplier"
                      data={this.props.supplierList}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="text-left mt-5 mb-3">
                          <div className="text-right">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="Pencarian"
                              style={{ margin: 0, top: "50%" }}
                            />
                          </div>
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            expandRow={expandRow}
                            pagination={paginationFactory()}
                          />
                          <ExportCSVButton
                            {...props.csvProps}
                            className="btn btn-primary"
                          >
                            Export CSV
                          </ExportCSVButton>
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : (
                    <div>
                      {this.props.error ? (
                        <div className="text-center">
                          <img
                            src={notrespond}
                            style={{ width: "30%" }}
                            alt="Not Respond
                          "
                          />
                          <br />
                          <h4>Request API Gagal, Silahkan Refresh</h4>
                        </div>
                      ) : (
                        <Skeleton width={"100%"} height={180} />
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(Supplier);
