const validationSupplier = (value) => {
  const errors = {};
  if (!value.kodeSupplier) {
    errors.kodeSupplier = "Tidak Boleh kosong";
  } else if (value.kodeSupplier > 5) {
    errors.kodeSupplier = "Tidak Boleh Lebih dari 5";
  }
  if (!value.namaSupplier) {
    errors.namaSupplier = "Tidak Boleh kosong";
  }
  if (!value.kodeKota) {
    errors.kodeKota = "Tidak Boleh kosong";
  }
  return errors;
};

export default validationSupplier;
